// D
export const deleteSingleChat = async(id) =>{
  const user = localStorage.getItem('user');
  try {
    const response = await api.delete(`/conversations/${id}`, {
      data: {
        user: user,
      }
    })
    return response.data;
  } catch (error) {
    console.error('Error delete single:', error);
    throw error;
  }
}